import React, { Component } from "react";
import {
  Tab,
  Tabs,
  Grid,
  Cell,
  Card,
  CardTitle,
  CardText,
  CardActions,
  Button,
  CardMenu,
  IconButton,
} from "react-mdl";

class Projects extends Component {
  constructor(props) {
    super(props);
    this.state = {
      activeTab: 0,
    };

    this.chatApplication = () => {
      window.location.assign(
        "https://deals.com.au"
      );
    };
    this.calculator = () => {
      window.location.assign(
        "https://www.efinancialcareers.sg"
      );
    };
    this.portfolio = () => {
      window.location.assign("https://www.brescadc.com/");
    };
    this.portfoliolive = () => {
      window.location.assign("https://devismail.netlify.app/");
    };
    this.ecommerce = () => {
      window.location.assign("https://sportunity.com/en");
    };
    this.leavemangement = () => {
      window.location.assign("https://www.hometown.in/");
    };
    this.Genius = () => {
      window.location.assign("https://geniusocean.com/");
    };
    this.Finder = () => {
      window.location.assign("https://homefinder.com/");
    };
    this.Food = () => {
      window.location.assign("https://fitfoodfresh.com");
    };
    this.Wallet = () => {
      window.location.assign("https://staging.mewbuilds.com/");
    };
    this.crudapplication = () => {
      window.location.assign(
        "https://github.com/ismail5g/BookListCrud_MVC-Asp.Net-Core"
      );
    };
    this.Alapa = () => {
      window.location.assign(
        "https://www.gohawaii.com/"
      );
    };
    this.Design = () => {
      window.location.assign(
        "https://www.hubsol.com/"
      );
    };
    this.toggleCategories = () => {
      if (this.state.activeTab === 0) {
        return (
          <div className="projects-grid">
            {/* Chat-Application */}
            <Card shadow={5} className="project-item">
              <CardTitle
                style={{
                  color: "#333",
                  height: "180px",
                  background: "url(images/chat.png) center / cover",
                }}
              >
                Touris Business
              </CardTitle>
              <CardText style={{ textAlign: "justify" }}>
                This is a Touris Business developed on react
              </CardText>
              <CardActions border>
                <Button
                  style={{ margin: "0 10px" }}
                  raised
                  ripple
                  primary
                  onClick={this.chatApplication.bind(this)}
                >
                  <i className="fa fa-github" aria-hidden="true" /> Connect
                </Button>

              </CardActions>
              <CardMenu style={{ color: "#fff" }}>
                <IconButton name="share" />
              </CardMenu>
            </Card>
            {/* Calculator */}
            <Card shadow={5} className="project-item">
              <CardTitle
                style={{
                  color: "#333",
                  height: "180px",
                  background: "url(images/2.png) center / cover",
                }}
              >
                Dream Career
              </CardTitle>
              <CardText style={{ textAlign: "justify" }}>
                This is Dream Career site developed on React and NodeJs
              </CardText>
              <CardActions border>
                <Button
                  style={{ margin: "0 10px" }}
                  raised
                  ripple
                  primary
                  onClick={this.calculator.bind(this)}
                >
                  <i className="fa fa-github" aria-hidden="true" /> Connect
                </Button>

              </CardActions>
              <CardMenu style={{ color: "#fff" }}>
                <IconButton name="share" />
              </CardMenu>
            </Card>
            {/* Portfolio */}
            <Card shadow={5} className="project-item">
              <CardTitle
                style={{
                  color: "#333",
                  height: "180px",
                  background: "url(images/3.png) center / cover",
                }}
              >
                restaurant
              </CardTitle>
              <CardText style={{ textAlign: "justify" }}>
                This is restaurant site developed on react.
              </CardText>
              <CardActions border>
                <Button
                  style={{ margin: "0 10px" }}
                  raised
                  ripple
                  primary
                  onClick={this.portfolio.bind(this)}
                >
                  <i className="fa fa-github" aria-hidden="true" /> Connect
                </Button>

              </CardActions>
              <CardMenu style={{ color: "#fff" }}>
                <IconButton name="share" />
              </CardMenu>
            </Card>
          </div>
        );
      } else if (this.state.activeTab === 1) {
        return (
          <div className="projects-grid">
            {/* Ecom-Application */}
            <Card shadow={5} className="project-item">
              <CardTitle
                style={{
                  color: "#333",
                  height: "180px",
                  background: "url(images/MERN1.png) center / cover",
                }}
              >
                Sport Life
              </CardTitle>
              <CardText style={{ textAlign: "justify" }}>
                This is an Sport Life developed on MERN Stack.
              </CardText>
              <CardActions border>
                <Button
                  style={{ margin: "0 10px" }}
                  raised
                  ripple
                  primary
                  onClick={this.ecommerce.bind(this)}
                >
                  <i className="fa fa-github" aria-hidden="true" /> Connect
                </Button>

              </CardActions>
              <CardMenu style={{ color: "#fff" }}>
                <IconButton name="share" />
              </CardMenu>
            </Card>
            {/* Leave-management */}
            <Card shadow={5} className="project-item">
              <CardTitle
                style={{
                  color: "#333",
                  height: "180px",
                  background: "url(images/MERN2.png) center / cover",
                }}
              >
                Hometown site
              </CardTitle>
              <CardText style={{ textAlign: "justify" }}>
                This is Hometown site developed on MERN Stack.
              </CardText>
              <CardActions border>
                <Button
                  style={{ margin: "0 10px" }}
                  raised
                  ripple
                  primary
                  onClick={this.leavemangement.bind(this)}
                >
                  <i className="fa fa-github" aria-hidden="true" /> Connect
                </Button>

              </CardActions>
              <CardMenu style={{ color: "#fff" }}>
                <IconButton name="share" />
              </CardMenu>
            </Card>
          </div>
        );
      }
      if (this.state.activeTab === 2) {
        return (
          <div>
            {/* Store-Procedure */}
            <Card shadow={5} className="project-item">
              <CardTitle
                style={{
                  color: "#333",
                  height: "180px",
                  background: "url(images/Vue1.png) center / cover",
                }}
              >
                Genius site
              </CardTitle>
              <CardText style={{ textAlign: "justify" }}>
                This is Genius site developed on Vue.js.
              </CardText>
              <CardActions border>
                <Button
                  style={{ margin: "0 10px" }}
                  raised
                  ripple
                  primary
                  onClick={this.Genius.bind(this)}
                >
                  <i className="fa fa-github" aria-hidden="true" /> Connect
                </Button>

              </CardActions>
              <CardMenu style={{ color: "#fff" }}>
                <IconButton name="share" />
              </CardMenu>
            </Card>
            <Card shadow={5} className="project-item">
              <CardTitle
                style={{
                  color: "#333",
                  height: "180px",
                  background: "url(images/Vue2.png) center / cover",
                }}
              >
                Home Finder
              </CardTitle>
              <CardText style={{ textAlign: "justify" }}>
                This is HomeFinder site developed on Vue.js.
              </CardText>
              <CardActions border>
                <Button
                  style={{ margin: "0 10px" }}
                  raised
                  ripple
                  primary
                  onClick={this.Finder.bind(this)}
                >
                  <i className="fa fa-github" aria-hidden="true" /> Connect
                </Button>

              </CardActions>
              <CardMenu style={{ color: "#fff" }}>
                <IconButton name="share" />
              </CardMenu>
            </Card>
            <Card shadow={5} className="project-item">
              <CardTitle
                style={{
                  color: "#333",
                  height: "180px",
                  background: "url(images/Vue3.png) center / cover",
                }}
              >
                Food Fresh
              </CardTitle>
              <CardText style={{ textAlign: "justify" }}>
                This is Food Fresh site developed on Vue.js.
              </CardText>
              <CardActions border>
                <Button
                  style={{ margin: "0 10px" }}
                  raised
                  ripple
                  primary
                  onClick={this.Food.bind(this)}
                >
                  <i className="fa fa-github" aria-hidden="true" /> Connect
                </Button>

              </CardActions>
              <CardMenu style={{ color: "#fff" }}>
                <IconButton name="share" />
              </CardMenu>
            </Card>
            <Card shadow={5} className="project-item">
              <CardTitle
                style={{
                  color: "#333",
                  height: "180px",
                  background: "url(images/Vue4.png) center / cover",
                }}
              >
                Wallet Site
              </CardTitle>
              <CardText style={{ textAlign: "justify" }}>
                This is Wallet site developed on Vue.js.
              </CardText>
              <CardActions border>
                <Button
                  style={{ margin: "0 10px" }}
                  raised
                  ripple
                  primary
                  onClick={this.Wallet.bind(this)}
                >
                  <i className="fa fa-github" aria-hidden="true" /> Connect
                </Button>

              </CardActions>
              <CardMenu style={{ color: "#fff" }}>
                <IconButton name="share" />
              </CardMenu>
            </Card>

          </div>
        );
      } else {
        return (
          <div>
            {/* facebook */}
            <Card shadow={5} className="project-item">
              <CardTitle
                style={{
                  color: "#333",
                  height: "180px",
                  background: "url(images/PHP1.png) center / cover",
                }}
              >
                Alaha
              </CardTitle>
              <CardText style={{ textAlign: "justify" }}>
                This Alaha developed on PHP.
              </CardText>
              <CardActions border>
                <Button
                  style={{ margin: "0 10px" }}
                  raised
                  ripple
                  primary
                  onClick={this.Alapa.bind(this)}
                >
                  <i className="fa fa-github" aria-hidden="true" /> Connect
                </Button>

              </CardActions>
              <CardMenu style={{ color: "#fff" }}>
                <IconButton name="share" />
              </CardMenu>
            </Card>
            <Card shadow={5} className="project-item">
              <CardTitle
                style={{
                  color: "#333",
                  height: "180px",
                  background: "url(images/PHP2.png) center / cover",
                }}
              >
                Web Design Company
              </CardTitle>
              <CardText style={{ textAlign: "justify" }}>
                This Web Design Company developed on PHP.
              </CardText>
              <CardActions border>
                <Button
                  style={{ margin: "0 10px" }}
                  raised
                  ripple
                  primary
                  onClick={this.Design.bind(this)}
                >
                  <i className="fa fa-github" aria-hidden="true" /> Connect
                </Button>

              </CardActions>
              <CardMenu style={{ color: "#fff" }}>
                <IconButton name="share" />
              </CardMenu>
            </Card>
          </div>
        );
      }
    };
  }

  render() {
    return (
      <div className="category-tabs">
        <Tabs
          activeTab={this.state.activeTab}
          onChange={(tabId) => this.setState({ activeTab: tabId })}
          ripple
        >
          <Tab>React JS</Tab>
          <Tab>MERN Stack</Tab>
          <Tab>Vue JS</Tab>
          <Tab>PHP</Tab>
        </Tabs>
        <Grid>
          <Cell col={12}>
            <div className="content">{this.toggleCategories()}</div>
          </Cell>
        </Grid>
      </div>
    );
  }
}

export default Projects;
