import React, { Component } from "react";
import { Grid, Cell } from "react-mdl";
import Education from "./Education";
import Experience from "./Experience";
import Skills from "./Skills";

class Resume extends Component {
  render() {
    return (
      <div>
        <Grid className="resume-gird">
          <Cell col={4}>
            <div style={{ textAlign: "center" }}>
              <img
                src="images/ismail.jpg"
                alt="avatar"
                style={{ height: "250px" }}
              />
            </div>

            <h2 style={{ paddingTop: "1em" }}>Michael Potter</h2>
            <h4 style={{ color: "grey" }}>React.js, Vue.js, Node.js and MySQL Specialist!</h4>
            <hr style={{ borderTop: "2px solid #833fb2", width: "85%" }} />
            <p style={{ textAlign: "justify" }}>
              I’m an enthusiastic, responsible, self-motivated and hard-working person.
              I’m able to work well under pressure and adhere to strict deadlines.
              Being worked for different projects helped me to adapt to the changes quickly and made me a mature team worker.
            </p>
            <hr style={{ borderTop: "2px solid #833fb2", width: "85%" }} />
          </Cell>
          <Cell className="resume-right-col" col={8}>
            <h2>Experience</h2>
            <Experience
              startYear={"May 2019"}
              endYear={"Presnt"}
              jobName={"MERN Stack Developer"}
              companyName={"freelancer"}
              jobDesc={["I worked on projects on these technologies Node.js, react.js, vue.js, express.js, Mongodb, AWS"]}
            />
            <Experience
              startYear={"April 2018"}
              endYear={"May 2019"}
              duration={12}
              jobName={"Web Developer"}
              companyName={"freelancer"}
              jobDesc={
                ["I worked on projects on these technologies Node.js, react.js, angular, express.js, Mongodb, AWS"]
              }
            />

            <hr style={{ borderTop: "3px solid #e22947" }} />
            <h2>Education</h2>
            <Education
              startYear={2013}
              endYear={2018}
              schoolName={"Ontario Tech University."}
              degreeName={"Master In Software Engineering"}
            />
            <hr style={{ borderTop: "3px solid #e22947" }} />
            <h2>Skills</h2>
            <Skills skill={"HTML/CSS"} progress={95} />
            <Skills skill={"NodeJS"} progress={90} />
            <Skills skill={"Angular"} progress={90} />
            <Skills skill={"Java"} progress={95} />
            <Skills skill={"JavaScript"} progress={95} />
            <Skills skill={"MySQL"} progress={80} />
            <Skills skill={"MongoDB"} progress={80} />
            <Skills skill={"React.js"} progress={80} />
            <Skills skill={"Vue.js"} progress={70} />
            <hr style={{ borderTop: "3px solid #e22947" }} />
          </Cell>
        </Grid>
      </div>
    );
  }
}

export default Resume;
