import React, { Component } from "react";

class Aboutme extends Component {
  render() {
    return (
      <div style={{ textAlign: "center", marginLeft: 'auto', color: '#fff', marginRight: 'auto', marginTop: '100px', width: '60%', background: 'gray', padding: '20px' }}>
        <h1 style={{ fontSize: '50px', textTransform: 'uppercase', fontFamily: 'Anton, sans-serif' }}>Michael Potter</h1>
        <p style={{ textAlign: 'justify', fontFamily: 'Oxygen, sans-serif' }}>
          I am Michael, a freelancer from Ahmedabad India and I am having 7+ years of web developing experience  development experience.

          I have expertise in AngularJS, ReactJS, and NodeJS specializing in MVPs for startups. I can help you with ReactJS, AngularJS front-end and create back-end as well to create a fully functional web app.

          In NodeJS I have 3+ years of experience and have created the back-end for multiple projects in NodeJS for Web.
        </p>
      </div>
    );
  }
}

export default Aboutme;
